<template>
	<div class="message_view" :style='{}'>
		<div class="back_view" v-if="centerType">
			<el-button class="back_btn" @click="backClick" type="primary"></el-button>
		</div>
		<div class="section_title">
            <span>{{formName}}</span>
		</div>
		<div class="messages_list">
			<div class="add_view">
				<el-button class="add_btn" @click="addClick" v-if="btnAuth('messages','')"></el-button>
			</div>
			<div class="messages" v-for="(item,index) in list" :key="index" @mouseenter="messageEnter(index)"
				@mouseleave="messageLeave">
				<div class="messages_user">
					<img v-if="item.avatarurl" class="messages_user_avatar"
						:src="$config.url + item.avatarurl.split(',')[0]" alt="">
					<img v-else class="messages_user_avatar" src="@/assets/avatar.png" alt="">
					<span class="messages_user_name">{{item.username}}</span>
				</div>
				<div class="messages_content" v-html="item.content"></div>
				<div class="messages_reply_view" v-if="item.reply">
					<el-divider content-position="left"></el-divider>
					<div class="meaages_reply" v-html="item.reply"></div>
				</div>
				<div class="messages_del_view">
					<div class="messages_del" @click="delClick(item.id)"
						v-if="messageShowIndex==index&&item.userid==userid"></div>
				</div>
				<el-divider v-if="index!=list.length - 1" />
			</div>
		</div>
		<el-pagination
			background 
			:layout="layouts.join(',')"
			:total="total" 
			:page-size="listQuery.limit"
            v-model:current-page="listQuery.page"
			prev-text=""
			next-text=""
			:hide-on-single-page="false"
			:style='{}'
			@size-change="sizeChange"
			@current-change="currentChange"/>
		<el-dialog v-model="formVisible" :title="''" width="50%" destroy-on-close>
			<el-form class="add_form" :model="form" label-width="80px">
				<el-row>
					<el-col :span="24">
						<el-form-item prop="content" label="">
							<editor :value="form.content"
								placeholder="" class="editor" @change="contentChange"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer>
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="formVisible=false"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"></el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>

<script setup>
	import {
		ref,
		nextTick,
		getCurrentInstance
	} from 'vue';
	import {
		ElMessageBox
	} from 'element-plus'
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//
	const tableName = 'messages'
	const formName = ''
	const router = useRouter()
	const route = useRoute()
	//
	//
	const btnAuth = (e, a) => {
		return context?.$toolUtil.isAuth(e, a)
	}
	const list = ref([])
	const listLoading = ref(false)
	const listQuery = ref({
		page: 1,
		limit: 20,
		sort: 'addtime',
		order: 'desc'
	})
	const searchQuery = ref({})
	const total = ref(0)
	const userid = ref(0)
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		context?.$http({
			url: `messages/list`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = res.data.data.total
		})
	}
	//
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
	//
	const centerType = ref(false)
	//
	const backClick = () => {
		router.push(`/index/${context?.$toolUtil.storageGet('frontSessionTable')}Center`)
	}
	const form = ref({
		content: '',
		userid: '',
		username: '',
		avatarurl: ''
	})
	const formVisible = ref(false)
	const resetForm = () => {
		form.value = {
			content: '',
			userid: context?.$toolUtil.storageGet('userid'),
			username: context?.$toolUtil.storageGet('frontName'),
			avatarurl: context?.$toolUtil.storageGet('headportrait') ? context?.$toolUtil.storageGet('headportrait') : ''
		}
	}
	//
	const addClick = () => {
		resetForm()
		formVisible.value = true
	}
	//
	const contentChange = (e) => {
		form.value.content = e
	}
	const save = () => {
		if (form.value.content == '') {
			context?.$toolUtil.message('', 'error')
			return false
		}
		let sensitiveWords = "";
		let sensitiveWordsArr = [];
		if(sensitiveWords) {
			sensitiveWordsArr = sensitiveWords.split(",");
		}
		for(var i=0; i<sensitiveWordsArr.length; i++){
			//
			var reg = new RegExp(sensitiveWordsArr[i],"g");
			//
			if (form.value.content.indexOf(sensitiveWordsArr[i]) > -1) {
				//  **
				form.value.content = form.value.content.replace(reg,"**");
			}
		}
		context?.$http({
			url: 'messages/add',
			method: 'post',
			data: form.value
		}).then(res => {
			context?.$toolUtil.message(``, 'success', () => {
				getList()
				formVisible.value = false
			})
		})
	}
	//
	const delClick = (id = null) => {
		if (id) {
			ElMessageBox.confirm(``, '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning',
			}).then(() => {
				context?.$http({
					url: `messages/delete`,
					method: 'post',
					data: [id]
				}).then(res => {
					context?.$toolUtil.message('', 'success', () => {
						getList()
					})
				})
			}).catch(_ => {})
		}
	}
	//
	const messageShowIndex = ref(-1)
	const messageEnter = (e) => {
		messageShowIndex.value = e
	}
	const meeageLeave = () => {
		messageShowIndex.value = -1
	}
	const init = () => {
		if (route.query.centerType) {
			centerType.value = true
		}
		userid.value = context?.$toolUtil.storageGet('userid')
		getList()
	}
	init()
</script>

<style lang="scss" scoped>
	// 
	.back_view {
		border-radius: 4px;
		padding: 10px 0px;
		margin: 10px auto;
		background: none;
		width: 100%;
		text-align: right;
		// 
		.back_btn {
			border: 1px solid var(--theme);
			cursor: pointer;
			border-radius: 0px;
			padding: 0 24px;
			color: #fff;
			background: var(--theme);
			width: auto;
			font-size: 14px;
			height: 34px;
		}
		// -
		.back_btn:hover {
		}
	}
	// 
	.messages_list {
		// 
		.add_view {
			// 
			.add_btn {
			}
			// -
			.add_btn:hover {
			}
		}
		// item
		.messages {
			// 
			.messages_user {
				// 
				.messages_user_avatar {
				}
				// 
				.messages_user_name {
				}
			}
			// 
			.messages_content {
			}
			// 
			.messages_reply_view {
				// 
				:deep(.el-divider) {
					// 
					.el-divider__text {
					}
				}
				// 
				.meaages_reply {
				}
			}
			// 
			.messages_del_view {
				// 
				.messages_del {
				}
				// -
				.messages_del:hover {
				}
			}
		}
	}
	// 
	.el-pagination {
		// 
		:deep(.el-pagination__total) {
		}
		// 
		:deep(.btn-prev) {
		}
		// 
		:deep(.btn-next) {
		}
		// 
		:deep(.btn-prev:disabled) {
		}
		// 
		:deep(.btn-next:disabled) {
		}
		// 
		:deep(.el-pager) {
			// 
			.number {
			}
			// 
			.number:hover {
			}
			// 
			.number.is-active {
			}
		}
		// sizes
		:deep(.el-pagination__sizes) {
			.el-select {
				//
				.select-trigger{
					height: 100%;
					.el-input{
						height: 100%;

					}
				}
			}
		}
		// 
		:deep(.el-pagination__jump) {
			// 
			.el-input {
			}
		}
	}
	// 
	.add_form {
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_editor {
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.formModel_cancel {
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
		}
		.formModel_confirm:hover {
		}
	}
</style>
